@import '../../../scss/variables';

.link {
	cursor: pointer;
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 20px;
	transition: color 0.2s ease;
	text-align: right;
	&:hover {
		color: $accent-color;
	}
}
.label {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 10px;
}
