@keyframes fade {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}

@keyframes fadeOut {
	from {
		opacity: 1;
	}

	to {
		opacity: 0;
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
		transform: translateX(-400px);
	}

	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
